import React from 'react';
import '../css/ProjectsStyle.css';
import { Link } from 'react-router-dom';
import N111Image from '../../images/N111.png';
import p222Image from '../../images/p222.png';
import s111Image from '../../images/s111.png';
import GitHubButton from '../GitHubButton';


export default function Projects() {
  return (
    <div className='Projects'>
      <h1 className='Projects-title'>Projekty</h1>

      <div className='ArticleSection' id='article3'>
        <h2 className='ArticleSection-title'><span>SM Trend</span></h2>
        <div className='ArticleSection-content'>
          
          <p className='ArticleSection-description'>
            <span>"SM Trend" to kompleksowa prezentacja aplikacji, zawierajca szczegowy opis funkcji, takich jak system logowania i rejestracji z wykorzystaniem JWT, moliwo tworzenia rnorodnych wpisw, zaawansowane filtry treci oraz narzdzia wspomagajce proces tworzenia i targetowania treci. Dodane zostay take zdjcia, ilustrujce interfejs uytkownika oraz filtry treci w aplikacji. W sekcji kodu zamieszczono przykad implementacji uwierzytelniania przy uyciu tokenu JWT.</span>
          </p>
          <img src={s111Image} alt='Zdjcie 3' className='ArticleSection-image' />
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
      <div style={{ marginRight: '20px' }}>
        <h3 style={{ marginBottom: '0', color: 'white' }}>FrontEnd</h3>
        <GitHubButton link="https://github.com/Mirac00/SM-Trend-Frontend.git" />
      </div>
      <div>
        <h3 style={{ marginBottom: '0', color: 'white' }}>BackEnd</h3>
        <GitHubButton link="https://github.com/Mirac00/SM-Trend-Backend.git" />
      </div>
    </div>
        <Link to='/SMTrend'>
          <button className='see-more-button'>Przejd do penego opisu SM Trend</button>
        </Link>
      </div>

      <div className='ArticleSection' id='article2'>
        <h2 className='ArticleSection-title'><span>Dokumentacja Projektu "React Portfolio"</span></h2>
        <div className='ArticleSection-content'>
          
          <p className='ArticleSection-description-width'>
            <span>
              Opis Projektu<br/>
              React Portfolio to aplikacja internetowa typu Single Page Application (SPA) stworzona w oparciu o bibliotek React. Gwnym celem aplikacji jest umoliwienie programicie zaprezentowania swojego portfolio oraz uatwienie kontaktu z potencjalnymi klientami lub pracodawcami. Projekt wykorzystuje rnorodne technologie i biblioteki do stworzenia dynamicznego interfejsu uytkownika (UI) oraz efektownych animacji.
              <br/>Zastosowanie Metodologii BEM<br/>W projekcie zastosowano metodologi BEM (Block, Element, Modifier) do nazewnictwa klas CSS w celu utrzymania spjnoci w kodzie oraz uatwienia zarzdzania stylami. Przykadowe klasyfikacje BEM mona znale w plikach CSS odpowiednich komponentw.
              <br/>Adres Strony i Repozytorium<br/>Strona internetowa projektu jest dostpna pod adresem: React Portfolio.<br/>Repozytorium projektu jest dostpne na platformie GitHub: Repozytorium GitHub<br/>Responsywno<br/>Projekt jest zaprojektowany z myl o responsywnoci. Dziaa na rnych urzdzeniach i dostosowuje si do rnych rozmiarw ekranw. Wykorzystuje techniki CSS, takie jak Media Queries, aby dostosowa wygld i ukad elementw w zalenoci od szerokoci ekranu.<br/>Przysze Rozwinicie Projektu<br/>W przyszoci planowane jest rozwinicie sekcji "Documents," gdzie bd umieszczone certyfikaty i dokumenty szkoleniowe programisty. Dziki temu uytkownicy bd mieli moliwo zapoznania si z jego osigniciami i kwalifikacjami.
            </span>
          </p>
          
        </div>
        <img src={p222Image} alt='Zdjcie 2' className='ArticleSection-image-width' />
        <GitHubButton link="hhttps://github.com/Mirac00/ReactPortfolio2023.git" />
        <Link to='/Fronttech'>
          <button className='see-more-button'>Przejd do penego opisu REACT Portfolio</button>
        </Link>
      </div>

      <div className='ArticleSection' id='article1'>
        <h2 className='ArticleSection-title'><span>Dokumentacja Aplikacji Notes.API</span></h2>
        <div className='ArticleSection-content'>
          <p className='ArticleSection-description'>
            <span>
              Wstp<br/>
              Aplikacja Notes.API to projekt oparty na platformie .NET Core, ktry oferuje API RESTful do zarzdzania notatkami i wydarzeniami. Jest to przykadowa aplikacja, ktra moe by uywana jako punkt wyjcia do tworzenia bardziej zaawansowanych systemw lub narzdzi zarzdzania informacjami. W tej dokumentacji omwimy wszystkie kluczowe elementy tej aplikacji, jej struktur i moliwe przypadki uycia.
              <br/>Przykady Uycia Komercyjnego:
              <ul>
                <li>System Zarzdzania Zadaniami</li>
                <li>System Kalendarza Firmowego</li>
                <li>System Magazynowy</li>
                <li>Aplikacja CRM</li>
              </ul>
              Podsumowanie: Aplikacja Notes.API to przykadowy projekt API RESTful do zarzdzania notatkami i wydarzeniami. Moe by uywana jako punkt wyjcia do tworzenia rnych systemw zarzdzania informacjami lub innych aplikacji, ktre wymagaj zarzdzania danymi. Moe by dostosowana i rozbudowana w zalenoci od potrzeb komercyjnych.
            </span>
          </p>
          <img src={N111Image} alt='Zdjcie 1' className='ArticleSection-image' />
        </div>
        <GitHubButton link="hhttps://github.com/Mirac00/Notes.API.git" />
        <Link to='/Netapi'>
          <button className='see-more-button'>Przejd do penego opisu Notes.Api</button>
        </Link>
      </div>

      <div className='NavigationButtons'>
        <button onClick={() => scrollToSection('article3')}>SM Trend</button>
        <button onClick={() => scrollToSection('article2')}>REACT Portfolio</button>
        <button onClick={() => scrollToSection('article1')}>.NET Notes.Api</button>
      </div>
    </div>
  );
}

function scrollToSection(sectionId) {
  const section = document.getElementById(sectionId);
  if (section) {
    window.scrollTo({
      top: section.offsetTop,
      behavior: 'smooth',
    });
  }
}
