import React from 'react';
import '../css/NetapiStyle.css';

export default function Netapi() {
  return (
    <>
      <div className='Netapi'>
        <h1 className='Netapi-title'>Dokumentacja Aplikacji Notes.API</h1>
      
      <h2 className='Netapi-section-title'>Wstp</h2>
      <p className='Netapi-text'>
        Aplikacja Notes.API to projekt oparty na platformie .NET Core, ktry oferuje API RESTful do zarzdzania notatkami i wydarzeniami. Jest to przykadowa aplikacja, ktra moe by uywana jako punkt wyjcia do tworzenia bardziej zaawansowanych systemw lub narzdzi zarzdzania informacjami. W tej dokumentacji omwimy wszystkie kluczowe elementy tej aplikacji, jej struktur i moliwe przypadki uycia.
      </p>

      <h2 className='Netapi-section-title'>Struktura Projektu</h2>
      <p className='Netapi-text'>
        Projekt GitHub Actions Notes.API: Projekt aplikacji API Notes.
      </p>

      <h2 className='Netapi-section-title'>Poczenie z Baz Danych</h2>
      <p className='Netapi-text'>
        SQL Server Database (ConnectionStrings: NotesDbConnectionString): Projekt jest poczony z baz danych SQL Server, gdzie przechowywane s informacje o notatkach, wydarzeniach i uytkownikach.
      </p>

      <h2 className='Netapi-section-title'>Zalenoci</h2>
      <p className='Netapi-text'>
        Analyzers: Analizatory kodu.
        <br />
        Frameworks: Frameworki platformy .NET Core.
        <br />
        Pakiety NuGet:
        <ul className='Netapi-list'>
          <li>Microsoft.EntityFrameworkCore.SqlServer (7.0.4): Biblioteka do obsugi bazy danych SQL Server w Entity Framework Core.</li>
          <li>Microsoft.EntityFrameworkCore.Tools (7.0.5): Narzdzia do Entity Framework Core.</li>
          <li>Microsoft.VisualStudio.Web.CodeGeneration.Design (6.0.13): Narzdzia do generowania kodu ASP.NET Core.</li>
          <li>Swashbuckle.AspNetCore (6.2.3): Biblioteka do generowania dokumentacji interfejsu API (Swagger).</li>
        </ul>
      </p>

      <h2 className='Netapi-section-title'>Waciwoci</h2>
      <p className='Netapi-text'>
        launchSettings.json: Konfiguracja ustawie startowych aplikacji.
      </p>

      <h2 className='Netapi-section-title'>Kontrolery</h2>
      <p className='Netapi-text'>
        AuthController.cs
        <br />
        Kontroler obsugujcy autoryzacj uytkownikw. Moe by rozbudowany o mechanizmy autentykacji, takie jak JWT.
        <br />
        Event111sController.cs
        <br />
        Kontroler do zarzdzania wydarzeniami (Event111). Pozwala na pobieranie, dodawanie, aktualizowanie i usuwanie wydarze.
        <br />
        NotesController.cs
        <br />
        Kontroler do zarzdzania notatkami (Note). Udostpnia funkcje do pobierania, dodawania, aktualizowania i usuwania notatek.
        <br />
        WeatherForecastController.cs
        <br />
        Kontroler obsugujcy prognozy pogody, uywany jako przykad danych do testowania aplikacji.
      </p>

      <h2 className='Netapi-section-title'>Warstwa Danych</h2>
      <p className='Netapi-text'>
        TableDbContext.cs
        <br />
        Klasa TableDbContext reprezentuje kontekst bazy danych. Zawiera DbSety dla notatek, wydarze i uytkownikw.
      </p>

      <h2 className='Netapi-section-title'>DTO (Data Transfer Objects)</h2>
      <p className='Netapi-text'>
        UserDto.cs
        <br />
        Klasa UserDto jest uywana do przesyania danych dotyczcych uytkownikw midzy klientem a serwerem. Zawiera pola, takie jak UserName i Password, ktre mona wykorzysta do uwierzytelniania uytkownikw.
      </p>

      <h2 className='Netapi-section-title'>Migracje</h2>
      <p className='Netapi-text'>
        20230523203617_InitialCreate.cs
        <br />
        Migracja inicjujca baz danych. Tworzy tabele dla notatek i wydarze.
        <br />
        TableDbContextModelSnapshot.cs
        <br />
        Plik zawierajcy migawk modelu bazy danych.
      </p>

      <h2 className='Netapi-section-title'>Modele i Encje</h2>
      <p className='Netapi-text'>
        Event111.cs
        <br />
        Klasa Event111 reprezentuje encj wydarzenia. Zawiera pola takie jak Title, Description, StartDate, EndDate i IsVisible.
        <br />
        Note.cs
        <br />
        Klasa Note reprezentuje encj notatki. Zawiera pola takie jak Title, Description i IsVisible.
        <br />
        User.cs
        <br />
        Klasa User reprezentuje encj uytkownika. Zawiera pola Username, PasswordHash i PasswordSalt.
      </p>

      <h2 className='Netapi-section-title'>Konfiguracja</h2>
      <p className='Netapi-text'>
        appsettings.json
        <br />
        Plik konfiguracyjny zawierajcy ustawienia aplikacji, takie jak poziomy logowania i acuchy pocze do bazy danych.
        <br />
        Program.cs
        <br />
        Gwny plik programu, ktry konfiguruje i uruchamia aplikacj. Skonfigurowane s tu usugi, takie jak Swagger do dokumentacji API.
      </p>

      <h2 className='Netapi-section-title'>Scenariusze Uycia</h2>
      <p className='Netapi-text'>
        Notatki i Wydarzenia
        <br />
        Gwnym celem aplikacji jest zarzdzanie notatkami i wydarzeniami. Moe by uywana jako narzdzie do organizacji zada lub kalendarz.
        <br />
        Autoryzacja Uytkownikw
        <br />
        Kontroler AuthController moe by rozbudowany o mechanizmy autentykacji, takie jak JWT, co pozwala na bezpieczne uwierzytelnianie uytkownikw.
        <br />
        Przykady Uycia Komercyjnego:
        <br />
        Pomimo e jest to przykadowa aplikacja, moe zosta wykorzystana jako punkt wyjcia do bardziej zaawansowanych projektw. Oto kilka przykadw komercyjnego wykorzystania:
        <ul className='Netapi-list'>
          <li>System Zarzdzania Zadaniami: Rozbudowa aplikacji Notes.API o dodatkowe funkcje zarzdzania zadaniami i projektami, co pozwala na stworzenie kompleksowego systemu zarzdzania zadaniami dla firm.</li>
          <li>System Kalendarza Firmowego: Rozbudowa aplikacji o funkcje zarzdzania kalendarzem firmowym, harmonogramem spotka i rezerwacjami zasobw.</li>
          <li>System Magazynowy: Rozbudowa aplikacji o funkcje zarzdzania magazynem, ledzenia zapasw, obsugi zamwie i dostaw, co pozwoli na utworzenie systemu magazynowego dla firm handlowych.</li>
          <li>Aplikacja CRM: Rozbudowa aplikacji o funkcje zarzdzania relacjami z klientami, ledzenia kontaktw i zarzdzania sprzeda, co pozwoli na utworzenie narzdzia do zarzdzania relacjami z klientami.</li>
        </ul>
      </p>

      <h2 className='Netapi-section-title'>Podsumowanie</h2>
      <p className='Netapi-text'>
        Aplikacja Notes.API to przykadowy projekt API RESTful do zarzdzania notatkami i wydarzeniami. Moe by uywana jako punkt wyjcia do tworzenia rnych systemw zarzdzania informacjami lub innych aplikacji, ktre wymagaj zarzdzania danymi. Moe by dostosowana i rozbudowana w zalenoci od potrzeb komercyjnych.
      </p>
      </div>
    </>
  );
}
