import React from 'react';
import '../css/SMTrendStyle.css';

export default function SMTrend() {
  return (
    <>
      <div className='SMTrend'>
        <div className='SMTrendSection'>
          <h1 className='SMTrendSection-title'>
            <span>Opis Aplikacji</span>
          </h1>
          <p className='SMTrendSection-description'>
            <span>
              SM Trend to aplikacja, ktra wspiera uytkownikw w tworzeniu treci internetowych, oferujc zaawansowane filtry, ktre pozwalaj na szybkie i atwe znalezienie najpopularniejszych treci w rnych kategoriach. Dziki tej aplikacji, proces tworzenia treci staje si bardziej efektywny i przynosi lepsze rezultaty.
            </span>
          </p>
        </div>

        <div className='SMTrendSection'>
          <h2 className='SMTrendSection-title'>
            <span>System Logowania i Rejestracji</span>
          </h2>
          <p className='SMTrendSection-description'>
            <span>
              Uytkownicy mog z atwoci zarejestrowa si lub zalogowa do aplikacji, co umoliwia dostp do penego zakresu funkcji. Proces uwierzytelniania jest zabezpieczony przy uyciu tokenw JWT, co gwarantuje bezpieczestwo danych.
            </span>
          </p>
          <div className='SMTrendSection-code'>
            <pre>
              <code>
{`// Przykadowy kod implementacji uwierzytelniania JWT
const authenticateUser = async (username, password) => {
  try {
    const response = await fetch('/api/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ username, password }),
    });
    const data = await response.json();
    if (data.token) {
      localStorage.setItem('token', data.token);
      // Uytkownik zalogowany
    } else {
      // Obsuga bdw logowania
    }
  } catch (error) {
    console.error('Bd podczas logowania:', error);
  }
};`}
              </code>
            </pre>
          </div>
        </div>

        <div className='SMTrendSection'>
          <h2 className='SMTrendSection-title'>
            <span>Postowanie Wpisw</span>
          </h2>
          <p className='SMTrendSection-description'>
            <span>
              SM Trend umoliwia tworzenie rnorodnych wpisw, takich jak dwiki, obrazy, filmy, sowa kluczowe itp. Uytkownicy mog dzieli si swoimi pomysami i treciami z innymi, co sprzyja budowaniu spoecznoci i zwiksza zasig ich twrczoci.
            </span>
          </p>
        </div>

        <div className='SMTrendSection'>
          <h2 className='SMTrendSection-title'>
            <span>Zaawansowane Filtry</span>
          </h2>
          <p className='SMTrendSection-description'>
            <span>
              Aplikacja oferuje zaawansowane filtry, ktre pozwalaj uytkownikom szybko odfiltrowa treci wedug popularnoci, kategorii, lub innych kryteriw. Dziki temu osoby tworzce materiay mog znale interesujce ich treci do swojej pracy.
            </span>
          </p>
        </div>

        <div className='SMTrendSection'>
          <h2 className='SMTrendSection-title'>
            <span>Ujednolicenie Procesu Tworzenia Treci</span>
          </h2>
          <p className='SMTrendSection-description'>
            <span>
              SM Trend skupia si na uatwieniu procesu komplementowania treci, ale take na ich targetowaniu, co ma na celu nie tylko przyspieszenie procesu tworzenia, ale rwnie zwikszenie jakoci tworzonych materiaw.
            </span>
          </p>
        </div>

        <div className='SMTrendSection'>
          <h2 className='SMTrendSection-title'>
            <span>Galeria Zdj</span>
          </h2>
          <div className='SMTrendSection-image'>
            <img src='path_to_image_1.jpg' alt='Zrzut ekranu aplikacji SM Trend, pokazujcy gwny interfejs uytkownika.' />
            <img src='path_to_image_2.jpg' alt='Widok filtra treci w aplikacji SM Trend.' />
          </div>
        </div>
      </div>
    </>
  );
}
