import React from 'react';
import '../css/DocumentsStyle.css';
import image1 from '../../images/dataimg.png';
import image2 from '../../images/netsec.png';

export default function Documents() {
  return (
    <>
      <div className='Documents'>
        <h1 className='Documents'>Certyfikaty</h1>
        <div className='imageContainer'>
          <p>Certyfikat 98-364: MTA Database Fundamentals - Ten certyfikat potwierdza podstawow wiedz i umiejtnoci zwizane z bazami danych, w tym z relacyjnymi bazami danych, takimi jak Microsoft SQL Server. Jest to kluczowe dla kadego programisty, ktry chce pracowa z danymi. Program MTA stanowi odpowiedni punkt wejcia do przyszej kariery w technologii. Jest to idealne dla osb, ktre dopiero zaczynaj swoj karier w IT. Aby zda egzamin 98-364, musisz mie podstawowe zrozumienie podstawowych koncepcji bazy danych, tworzenia obiektw bazy danych, manipulowania danymi, przechowywania danych i administrowania baz danych. To pokazuje, e jeste zdolny do zarzdzania danymi na poziomie podstawowym. Egzamin skada si z 45-50 pyta, a na jego ukoczenie masz 120 minut (2 godziny). To pokazuje twoj zdolno do radzenia sobie z presj czasu. Egzamin jest przeznaczony dla kandydatw, ktrzy chc udowodni wprowadzajc wiedz na temat bazy danych. To pokazuje twoje zaangaowanie w nauk i rozwijanie swoich umiejtnoci. Wszystkie egzaminy zwizane z certyfikacjami Microsoft Technology Associate (MTA) zostan wycofane 30 czerwca 2022 roku. To pokazuje twoje zaangaowanie w cige uczenie si i dostosowywanie si do zmieniajcych si technologii.</p>
          <img src={image1} alt='Certyfikat 98-364: MTA Database fundamentals' />
        </div>
        <div className='imageContainer'>
          <p>Certiport Network Security - Posiadacze tego certyfikatu wykazuj podstawow wiedz i umiejtnoci z zakresu bezpieczestwa, w tym zrozumienie podstawowych zasad bezpieczestwa, bezpieczestwa systemu operacyjnego, bezpieczestwa sieci i urzdze oraz bezpiecznych praktyk informatycznych. Bezpieczestwo jest kluczowe dla kadego programisty, a ten certyfikat pokazuje twoje zaangaowanie w ochron danych. Program IT Specialist jest skierowany do kandydatw, ktrzy rozwaaj lub dopiero zaczynaj karier w technologii informacyjnej. To pokazuje twoje zaangaowanie w rozwijanie swojej kariery. Kandydaci mog potwierdzi swoj wiedz na szeroki zakres tematw IT, w tym rozwj oprogramowania, administracja bazami danych, sieci i bezpieczestwo, mobilno i zarzdzanie urzdzeniami oraz kodowanie. To pokazuje twoj wszechstronno jako programista.</p>
          <img src={image2} alt='Certyfikat Certiport Network Security' />
        </div>
      </div>
    </>
  );
}
